// - - - - - - - - - - Foundations

@import "_reset";
@import "_mixins";
@import "_plugins";



// - - - - - - - - - - Variables


// Colors

$background-color: #ffffff;
$background-alt-color: #f4f5f6;
$border-color: #dddddd;

$text-dark-color: #2A2F36;
$text-medium-color: #6C7A89;
$text-light-color: #ABB7B7;

$accent-color: #F2784B;

$overlay-background-color: #F2784B;
$overlay-text-color: #ffffff;
$overlay-accent-color: #2A2F36;

$error-color: #D64541;


// Fonts

$body-font: "Muli", sans-serif;
$title-font: "Muli", sans-serif;

$regular-weight: 400;
$bold-weight: 600;


// Typography

$sitetitle-small: 25px;
$sitetitle-medium: 30px;
$sitetitle-large: 35px;
$sitetitle-weight: 700;
$sitetitle-spacing: -0.02em;

$menu-small: 30px;
$menu-medium: 15px;
$menu-large: 17px;
$menu-weight: 600;
$menu-spacing: 0em;

$grid-item-title-small: 22px;
$grid-item-title-medium: 30px;
$grid-item-title-large: 35px;
$grid-item-title-weight: 700;
$grid-item-title-spacing: -0.02em;

$grid-item-subtitle-small: 15px;
$grid-item-subtitle-medium: 17px;
$grid-item-subtitle-large: 19px;
$grid-item-subtitle-weight: 400;
$grid-item-subtitle-spacing: 0em;

$h1-small: 25px;
$h1-medium: 35px;
$h1-large: 45px;
$h1-weight: 700;
$h1-spacing: -0.02em;
$h1-lineheight: 1.2;

$h2-small: 22px;
$h2-medium: 30px;
$h2-large: 35px;
$h2-weight: 700;
$h2-spacing: -0.02em;
$h2-lineheight: 1.2;

$h3-small: 22px;
$h3-medium: 25px;
$h3-large: 30px;
$h3-weight: 600;
$h3-spacing: -0.02em;
$h3-lineheight: 1.3;

$h4-small: 17px;
$h4-medium: 22px;
$h4-large: 22px;
$h4-weight: 700;
$h4-spacing: 0em;
$h4-lineheight: 1.6;

$h5-small: 17px;
$h5-medium: 20px;
$h5-large: 20px;
$h5-weight: 700;
$h5-spacing: 0em;
$h5-lineheight: 1.6;

$h6-small: 17px;
$h6-medium: 20px;
$h6-large: 20px;
$h6-weight: 700;
$h6-spacing: 0em;
$h6-lineheight: 1.6;

$p-small: 15px;
$p-medium: 17px;
$p-large: 19px;
$p-weight: 400;
$p-spacing: 0em;
$p-lineheight: 1.6;

$blockquote-small: 25px;
$blockquote-medium: 35px;
$blockquote-large: 45px;
$blockquote-weight: 300;
$blockquote-spacing: -0.02em;
$blockquote-lineheight: 1.6;

$button-weight: 600;
$button-spacing: 0em;


// Logo
$logo-width: 200px;
$footer-logo-width: 200px;


// Grid item
$overlay-opacity: 0.95;
$grid-spacing: 20px;

.tucker-header-title {
    font-size: $sitetitle-small;
    font-weight: $sitetitle-weight;
    letter-spacing: $sitetitle-spacing;
    line-height: 1;
    color: $text-dark-color;

    @include mq(tabletp) {
        font-size: $sitetitle-medium;
    }

    @include mq(laptop) {
        font-size: $sitetitle-large;
    }
}

.tucker-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tucker-gallery {
    display: grid;
    margin: 0 20px 20px 20px;
    width: calc(100vw - 60px);
    padding: 0 0 20px 0;
    grid-template-columns: 100%;
    grid-gap: 20px;

    @include mq(phone1) {
        grid-template-columns: 100%;
    }


    @include mq(tabletp) {
        grid-template-columns: 50% 50%;
    }

    @include mq(laptop) {
        grid-template-columns: 33% 33% 33%;
    }
}

.tucker-gallery-content {
    @include transition(all 0.2s ease-in-out);
}

.tucker-vid {
    width: 100%;
    height: 100%;
}

.tucker-hide-project-list {
    display: none;

    @include mq(laptop) {
        display: block;
    }
}

.tucker-project-list-container {
    margin-top: 25px;
    text-align: center;
    width: 75%;
}

.tucker-project-list {
    list-style: none;
    margin: 0 auto;
    padding: 30px;
}

.tucker-project-list-item {
    display: inline-block;
    margin-left: 20px;

    &:first-child {
        margin-left: 0;
    }
}

.tucker-list-item-link {
    font-size: $menu-medium;
    font-weight: $menu-weight;
    color: $text-dark-color;
    padding: 10px 0;

    &:hover {
        color: $accent-color;
    }

    padding: 0 0 3px;
    font-size: $menu-large;
    color: $text-dark-color;
    border-bottom: 2px solid transparent;

    @include mq(laptop) {
    }
    
    &.active-link {
        color: $text-dark-color;
        border-color: $accent-color;
    }
}

.about-me {
    display: flex;
}

.about-me-content {
    flex: 1 1 auto;
}

.profile-pic {
    flex: 0 0 100px;
    width: 100px;
    height: 100px;
    margin: 0 auto 35px auto;
}

.resume-content {
    overflow: auto;
}

.resume-container {
    margin: 40px auto 0 auto;
    padding: 0 60px;
    min-width: 816px;
    max-width: 816px;
    color: black;
    font-family: "Times New Roman";
}

.resume-name {
    text-align: center;
    font-size: 14pt;
    font-weight: 700;
    margin-bottom: 15px;
}

.resume-entry {
    margin-bottom: 15px;
}

.resume-section-header {
    font-size: 14pt;
    font-weight: 700;
    margin-bottom: 7px;
}

.resume-entry-header {
    font-size: 10pt;
    font-weight: 700;
    display: flex;
    justify-content: space-between;
    line-height: 1.2;

    .title {
        flex: 0 0 50%;
        font-style: italic;
        align-self: flex-start;
    }

    .education-title {
        flex: 0 0 50%;
        font-weight: 300;
        align-self: flex-start;
    }

    .date {
        align-self: flex-end;
    }
}

.resume-entry-title {
    font-size: 10pt;
    font-weight: 700;
    line-height: 1.2;

    a {
        margin-left: 5px;
        color: #F2784B;
    }
}

.resume-entry-list {
    font-size: 10pt;
    margin-left: 33px;
    margin-top: 2px;

    li {
        list-style-position: outside;
        line-height: 1.3;
    }
}

// - - - - - - - - - - Global styles

@import "_basic";

@import "_includes/_header";
@import "_includes/_footer";

@import "_includes/_content";
@import "_includes/_socials";
@import "_includes/_gallery";
@import "_includes/_syntax";
@import "_includes/_contact-form";



// - - - - - - - - - - Section styles

@import "_includes/_portfolio";
@import "_includes/_blog";


